import Vue from 'vue'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'

// 承载
import activityOfflineDetail from '../pages/activityOfflineDetail.vue'
// 主页
import activityOfflineDetailIndex from '../views/activity-offline-detail.vue'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'activityOfflineDetail',
    component: activityOfflineDetailIndex
  }]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#activityOfflineDetail',
  router,
  components: {
    activityOfflineDetail
  },
  template: '<activityOfflineDetail/>'
})



